body {
  font-family: Lato;
  color: #666;
}

input{
border-radius: 8px; 
border: none;
padding:6px 16px;
}

button{
border-radius: 8px;
border: none;
padding:4px 16px;
margin:0px 2px;
}

button:hover{
background-color: aqua;
color:white;
cursor:pointer;  
}
